.dialog-title{
  width: 564px;
  .dialog-winner{
    > img {
      margin-left: 63px; 
    }
  }
  .dialog-chess {
    >img{
      display: block;
      width: 240px;
      height: 250px;
      margin: 0 auto 9px;
    }
  }
  .dialog-who-play {
    > img {
      display:block;
      margin: 0 auto;
    }
  }
}
.dialog-center {
  justify-content: center;
}
.dialog-main{
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 866px;
  height: 320px;
  padding: 50px 80px;
  margin-left:80px; 
  background-image: url('../../static/img/bubble/dialog.png');
  background-size: 100% 100%;
  font-family: 'STKaiti';
  .dialog-header{
    font-size: 50px;
    text-align: center;
    color: rgb(163, 93, 89);
    margin:15px 0; 
  }
  .dialog-content{
    width:100%;
    font-size: 48px;
    line-height: 66px;
  }
  .dialog-btn{
    width: 65px;
    height: 65px;
    vertical-align: middle;
  }
}