@mixin center {
  position: absolute;
  top: 400px;
  left: 50%;
  transform: translate(-50%, -50%); 
}
@mixin bgsize {
  background-size:100% 100%;
  background-repeat: no-repeat;
}
@mixin chesssize {
  width: 276px;
  height: 286px;
}
$chess-white:'../../static/img/countdown/white.png';
$chess-black:'../../static/img/countdown/black.png';
$number-vs:'../../static/img/countdown/vs.png';
$number-1:'../../static/img/countdown/1.png';
$number-2:'../../static/img/countdown/2.png';
$number-3:'../../static/img/countdown/3.png';
$bg-1:'../../static/img/countdown/bg1.png';
$bg-2:'../../static/img/countdown/bg2.png';
$bg-3:'../../static/img/countdown/bg3.png';

.count-wrap{
  width: 100%;
  height: 100%;
  position: relative;
  .count-chess1{
    @include chesssize;
    @include bgsize;
    background-image: url($chess-white);
    position: absolute;
    top: 207px;
    right: -50px;
    animation: chess1 1s forwards linear;
    z-index: 5;
  }
  .count-chess2{
    @include chesssize;
    @include bgsize;
    background-image: url($chess-black);
    position: absolute;
    top: 207px;
    left: 50px;
    animation: chess2 1s forwards linear;
    z-index: 5;
  }
  .count-number{
    @include chesssize;
    @include bgsize;
    position: absolute;
    left: 50%;
    top: 207px;
    transform: translateX(-50%);
    //background-image: url($number-vs);
    animation: number 4s forwards linear;
    z-index: 5;
  }
  .count-bg1{
    @include center;
    @include bgsize;
    width: 663px;
    height: 758px;
    background-image: url($bg-1);
  }
  .count-bg2{
    @include center;
    @include bgsize;
    background-image: url($bg-2);
    animation: bg2 4s forwards linear;
  }
  .count-bg3{
    @include center;
    @include bgsize;
    width: 700px;
    height: 700px;
    background-image: url($bg-3);
    animation: bg3 4s forwards linear;
  }
}

@keyframes bg2 {
  from {
    width: 684px;
    height: 671px;
  }
  to{
    width: 982px;
    height: 966px;
  }
}
@keyframes bg3 {
  0%{
    opacity: .5;
  }
  3% {
    opacity: 0;
  }
  25%{
    opacity: .5;
  }
  28%{
    opacity: 0;  
  }
  50%{
    opacity: .5;
  }
  53%{
    opacity: 0;
  }
  75%{
    opacity: .5;
  }
  78%{
    opacity: 0;
  }
  100% {
    opacity: .5;
  }
}
@keyframes number {
  0%{
    opacity: 1;
  }
  3% {
    opacity: 0;
  }
  5% {
    opacity: 0;
    background-image: url($number-vs);
  }
  25%{
    opacity: 1;
    background-image: url($number-vs);
  }
  28%{
    opacity: 0;  
    background-image: url($number-vs);
  }
  30%{
    opacity: 0;
    background-image: url($number-3);
  }
  50%{
    opacity: 1;
    background-image: url($number-3);
  }
  53%{
    opacity: 0;
    background-image: url($number-3);
  }
  55%{
    opacity: 0;
    background-image: url($number-2);
  }
  75%{
    opacity: 1;
    background-image: url($number-2);
  }
  78%{
    opacity: 0;
    background-image: url($number-2);
  }
  80%{
    opacity: 0;
    background-image: url($number-1);
  }
  100% {
    opacity: 1;
    background-image: url($number-1);
  }
}
@keyframes chess1 {
  0% {
    right: -50px;
  }
  70%{
    right: 822px;
  }
  100%{
    right:500px;
  }
}
@keyframes chess2 {
  0%{
    left: -50px;
  }
  70%{
    left: 822px;
  }
  100%{
    left: 500px
  }
}