@mixin item {
  width: 390px;
  height: 350px;
  padding: 15px 10px;
  box-sizing: border-box;
}
.wrap{
  height: 100%;
  .menu-wrap{
    width: 1088px;
    zoom: 1;
    margin: 294px 416px 0;
    &::after{
      display: block;
      content: '';
      clear: both;
      visibility: hidden;
    }
    .goGame{
      @include item;
      float: left;
    }
    .goSetting{
      @include item;
      float: right;
      
    }
  }
  
  .instruction{
    width: 1427px;
    height: 954px;
    margin: 0 auto;
    background-image:url('../../static/img/home/tip_touch.png');
    background-size: 100% 100%; 
  }
}