@mixin flex {
  display: flex;
}
.chess-bg{
  height: 100%;
  position: relative;
  margin-top: 35px;
  .chess-layout-flex{
    @include flex();
  }
  .step-1{
    margin: 152px 361px 0;
    > div{
      &:nth-child(2) {
        margin-left:242px;
      }
    }
  }
  .step-2{
    width: 1004px;
    margin: 152px auto 0;
    .game-item {
      width: 394px;
      height: 394px;
      img {
        display: block;
        margin: 0 auto;
      }
    }
    > div{
      &:nth-child(2) {
        margin-left:166px;
      }
    }
  }
  .step-3{
    flex-direction: column;
    &.weiqi{
      margin-top: 161px;
      > div{
        &:nth-child(2) {
          margin-top:52px;
        }
      }
      .game-item{
        margin-right:28px;
      }
    }
    &.wuziqi{
      margin-top: 113px;
      .game-item{
        margin-left:133px;
        margin-right:133px;
        padding: 45px;
      }
      > div {
        &:nth-child(2) {
          margin-top:78px;
        }
      }
    }
    > div{
      display: flex;
      justify-content: center;
    }
  }
}